import React, { useState } from 'react'
import Emoji from './Emoji'
import './App.css';

function App() {
  const [input, setInput] = useState('I am a palindrome')
  const [userInputs, setUserInputs] = useState('')

  const [isPalindrome, setIsPalindrome] = useState(false)
  const [isSentence, setIsSentence] = useState(true)

  const [isSubmited, setIsSubmited] = useState(false)

  // Palindrome
  let result = false;
  function palindrome(str) {

    /**
     * Replaces any special character, underscore or white space with empty character
     * and makes whole string lowercase.
     * This way it's way more easier to work with string
     */
    str = str.replace(/[^A-Za-z0-9]/gi, '').toLowerCase();
    // Done replacing

    // same str
    let strArray = [];
    for (let i = str.length - 1; i >= 0; i--) {
      strArray.push(str[i]);
    }

    // reversed str
    let strReversed = [];
    for (let j = 0; j < str.length; j++) {
      strReversed.push(str[j]);
    }

    //  Comparing str and reversed str
    if (strArray.join('') === strReversed.join('')) {
      result = true;
      // console.log('Palindrome')
      setIsPalindrome(result)
    }
    // console.log('Not palindrome')
    setIsPalindrome(result);
  }

  // Palindrome

  const handleChange = e => {
    e.persist()
    setInput(`${e.target.value}`)
  }

  let userInput = ''
  React.useEffect(() => {
    //eslint-disable-next-line
    userInput = userInput + input
    setIsSentence(/^\w+\s/.test(userInput))
    palindrome(userInput)

    setUserInputs(userInput)
  }, [input]);

  const submitFunc = e => {
    e.preventDefault()
    setIsSubmited(true)
    setTimeout(() => {setIsSubmited(false)}, 1000)
  }

  return (
    <div id="App">
      <h1>Palindrome Checker</h1>

      <form>
        <label>Enter your text:</label>
        <br />
        <textarea id="textarea" name="textarea" rows="4" cols="45" value={input} onChange={handleChange} />
        <br />
        <button id="submit-btn" onClick={submitFunc}>Submit</button>
        {isSubmited
          ? <p id="palindrome">{userInputs === ''
            ? <span>Is whitespace palindrome? I don't know <Emoji symbol="ðŸ˜" /></span>
            : isSentence
              ? <span>Your sentence is
            {isPalindrome === true ? <span style={{color: 'green'}}> a palindrome!</span> : <span style={{color: 'red'}}> not a palindrome!</span>}</span>
              : <span>Your word is
            {isPalindrome === true ? <span style={{color: 'green'}}> a palindrome!</span> : <span style={{color: 'red'}}> not a palindrome!</span>}</span>}
          </p>
          : null}
      </form>
      <br />
    </div>
  );
}

export default App;
